import React from 'react';
import "./HeadCarousel.scss"

const HeadCarousel = (props) => {
  return (
     <div>
        {/* Carousel */}
       <div className='head-carousel'>
          <div className='container'>
             <div className='text-container'>
                 <h1>{props.name}</h1>
                 <h3>Home / {props.name}</h3>
             </div>
          </div>   
       </div>
       {/* Carousel */}






     </div>
  ) 
};

export default HeadCarousel;
