import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import "./Navbar.scss"
import { Icon } from '@iconify/react';


window.addEventListener("scroll", function() {
  var header = document.querySelector(".navbar");
  header.classList.toggle("sticky", window.scrollY > 0);
})


function Navbar() {
  const [click, setClick] = useState(false);

  const handleClick = () => setClick(!click);
  return (
    <>
      <nav className="navbar">



       {/* Nav-container-top*/}
      <div className="nav-container-top">
        <div className="texts">
          <span>Contact(97)450-45-65</span>
          <span>Email:example@gmail.com</span>
            
        </div>
      </div>
    
       {/* Nav-container-top*/}  







       {/* Nav-container-under */}
        <div className="nav-container-under">
          <NavLink exact to="/" className="nav-logo">
              <img src="https://demo.xpeedstudio.com/carrental/wp-content/uploads/2020/06/car_rental_logo.gif" alt="carpicture"/>
          </NavLink>

          <ul className={click ? "nav-menu active" : "nav-menu"}>
            <div className="nav-item-div">
            <li className="nav-item">
              <NavLink
                exact
                to="/"
                activeClassName="active"
                className="nav-links"
                onClick={handleClick}
              >
                Home
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                exact
                to="/about"
                activeClassName="active"
                className="nav-links"
                onClick={handleClick}
              >
                About
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                exact
                to="/rentcar"
                activeClassName="active"
                className="nav-links"
                onClick={handleClick}
              >
                Viehicle Models
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                exact
                to="/testimionals"
                activeClassName="active"
                className="nav-links"
                onClick={handleClick}
              >
                Testimionals
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                exact
                to="/team"
                activeClassName="active"
                className="nav-links"
                onClick={handleClick}
              >
              Team
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                exact
                to="/contact"
                activeClassName="active"
                className="nav-links"
                onClick={handleClick}
              >
                Contact
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                exact
                to="/faqs"
                activeClassName="active"
                className="nav-links"
                onClick={handleClick}
              >
              FAQs
              </NavLink>
            </li>
            <div className="sign-link">
         
            <NavLink
            className="signin"
                exact
                to="/signin"
              >
                Sign In
              </NavLink>
            
             <NavLink
              className="signup"
                exact
                to="/signup"
              >
                Sign Up
              </NavLink>
            
            </div>
            </div>
      
         
          </ul>
          </div>

          {/* Nav-container-under */}
      
          <div className="nav-icon" onClick={handleClick}>
            {/* <i className={click ? "fa fa-times" : "fa fa-bars"}></i> */}
            <Icon icon="codicon:three-bars" />
          </div>
      
      </nav>

      </>
  );
}

export default Navbar;
