import React from 'react';
import "./SignUp.scss";
import Fade from 'react-reveal/Fade';
import { NavLink } from 'react-router-dom';
import { Icon } from '@iconify/react';


const SignUp = () => {
  return   <Fade bottom>
  
       <div className='input-section'>
                
      
         <div className='input-container'>
           <div className='input-text'>
             <div className='input-text-div'>
                <h2>SIGN UP FOR AMAZING OFFERS</h2>
                <h3>EXCLUSIVE ACCESS FOR OFFERS AND PROMOTIONS</h3>
             </div>
              </div>

              <div className='input-center'>
                
                <input className='input-form' type="text" placeholder="Enter Your Email Address" name="email" required/>
                 
                 <NavLink  exact
                to="/contact"><button>SEND</button></NavLink>
              </div>

              <div className='messangers'>
                  <div className='messangers-container'>
                    <a href='https://www.skype.com' className='twitter'>
                    <Icon className="fa-icon" icon="fontisto:twitter" />
                    </a>
                    <a href='https://www.dribbble.com' className='dribble'>
                    <Icon className="fa-icon" icon="akar-icons:dribbble-fill" />
                    </a>
                    <a href='https://www.facebook.com' className='facebook'>
                    <Icon className="fa-icon"  icon="cib:facebook-f"/>

                    </a>
                  </div>
              </div>
         
           </div>
    </div>;
  </Fade>
};

export default SignUp;
