import React, { useState } from "react";
// import { useNavigate } from "react-router-dom";
import "./SignIn.scss"
import { NavLink } from "react-router-dom";

const SignIn = () => {

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  // const history = useNavigate();

  const signin = async (e) => {
    e.preventDefault();
    const data = {
      email: email,
      password: password,
    };

    const responce = await fetch("http://81.95.232.205:8023/api/login/", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    });
    const reg = await responce.json();
    console.log(reg);
    localStorage.setItem("token", reg.token);
    // history("/");
  };






  return (
     <div className='center-sect'>
     <div className="center">
   <h1>Sign In</h1>
   <form method="post">
     <div className="txt_field">
       <input onChange={(e) => setEmail(e.target.value)} type="text" required/>
       <span></span>
       <label>Username</label>
     </div>
     <div className="txt_field">
       <input onChange={(e) => setEmail(e.target.value)} type="password" required/>
       <span></span>
       <label>Password</label>
     </div>
     <div className="pass">Forgot Password?</div>
     <input onChange={(e) => setEmail(e.target.value)} type="submit" value="Login"/>
     <div className="signup_link">
     <NavLink
                exact
                to="/signup"

              >
                Register
      </NavLink>
     </div>
   </form>
 </div>
 </div>
  )
}

export default SignIn