import React, {useState} from 'react';
import CarsComponent from './CarsComponent';
import CarsData from './CarsData';
import "./VehicleModels.scss"
import Fade from 'react-reveal/Fade';

const VehicleModels = () => {

  const [active, setActive] = useState("FirstCard")

  return (
   <div className='rent-section'>

    
     <div className='important-section'> 

     <div className='head-text'>

     <span>Vehicle Models - Our rental fleet at a glance</span>

     </div>
    <div className='important-div'>
    <Fade left>
        <div className='right-div'>
           <button onClick={() => setActive("FirstCard")}>VW Golf VII</button>
           <button onClick={() => setActive("SecondCard")}>Audi A1 S-LINE</button>
           <button onClick={() => setActive("ThirdCard")}>Toyota Camry</button>
           <button onClick={() => setActive("FourthCard")}>BMW 320 ModernLine</button>
           <button onClick={() => setActive("FifthCard")}>Mercedes-Benz GLK</button>
           <button onClick={() => setActive("SixCard")}>VW Passat CC</button>
        </div>
    </Fade>

     <div>

       {active === "FirstCard" && <CarsComponent data={CarsData} cardIndex={0} />}
       {active === "SecondCard" && <CarsComponent data={CarsData} cardIndex={1} />}
       {active === "ThirdCard" && <CarsComponent data={CarsData} cardIndex={2} />}
       {active === "FourthCard" && <CarsComponent data={CarsData} cardIndex={3} />}
       {active === "FifthCard" && <CarsComponent data={CarsData} cardIndex={4} />}
       {active === "SixCard" && <CarsComponent data={CarsData} cardIndex={5} />}
          
     </div>

     </div>



    </div>
   </div>
  );
};

export default VehicleModels;
