import React from 'react';
import "./VehicleModels.scss"
import { NavLink } from 'react-router-dom';
import Zoom from 'react-reveal/Zoom';

const CarsComponent = ({data, cardIndex}) => {
  return (
     <div>
     <Zoom>
        <div>
           {data[cardIndex].map(item => (
              <div className='info-section'>

                 <div className='center-div'>
                   <img src={item.image} alt='aaa'/>
                 </div>


                 <div className='left-div'>
                 <div className='price'>
                         $ {item.price} rent per day     
                  </div>
                  <table>
                    
                     <tr>
                        <td>Model</td>
                        <td>{item.model}</td>
                     </tr>
                     <tr>
                        <td>Doors</td>
                        <td>{item.doors}</td>
                     </tr>
                     <tr>
                        <td>Seats</td>
                        <td>{item.seats}</td>
                     </tr>
                     <tr>
                        <td>Luggage</td>
                        <td>{item.luggage}</td>
                     </tr>
                     <tr>
                        <td>Transmission</td>
                        <td>{item.transmission}</td>
                     </tr>
                     <tr>
                        <td>Air Conditing</td>
                        <td>{item.conditing}</td>
                     </tr>
                     <tr>
                        <td>Minimum age</td>
                        <td>{item.age}</td>
                     </tr>
                  </table>
                   {/* <Switch>
                      <Route > */}
                  <NavLink to="/rentcar" className="cars-btn">
      
                  <div>
                    <span class="fa fa-calendar"></span>
                         Reserve now 
                  </div>
                    
                   </NavLink>
                   {/* </Route>
                   </Switch> */}
                 </div>       
              </div>


        
                    

           ))}
        </div>

      
        </Zoom>
      </div>
  
  );
};

export default CarsComponent;
