import React, { useState } from "react";
// import { useNavigate } from "react-router-dom";
import "./SignUp.scss"
import { NavLink } from 'react-router-dom'

const SignUp = () => {

  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  // const history = useNavigate();

  const signup = async (e) => {
    e.preventDefault();
    const data = {
      username: username,
      email: email,
      password: password,
    };

    const responce = await fetch("http://81.95.232.205:8021/register/", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    });
    const reg = await responce.json();
    console.log(reg);
    localStorage.setItem("token", reg.token);
    // history("/");
  };


  
  return (
   <div className='center-sect-2'>
   <div className="center-2">
 <h1>Sign Up</h1>
 <form method="post">
   <div className="txt_field-2">
     <input type="text" required/>
     <span></span>
     <label>Name</label>
   </div>
   <div className="txt_field-2">
     <input type="password" required/>
     <span></span>
     <label>Last Name</label>
   </div>
   <div className="txt_field-2">
     <input type="text" required/>
     <span></span>
     <label>Username</label>
   </div>
   <div className="txt_field-2">
     <input type="text" required/>
     <span></span>
     <label>Password</label>
   </div>
    <input type="submit" value=" Register"/>
   <div className="signup_link-2">
   <NavLink
              exact
              to="/signin"

            >
                   SignIn
    </NavLink>
   </div>
 </form>
</div>
</div>
  )
}

export default SignUp