import React from "react"
import {
  BrowserRouter as Router,
  Route,
  Switch,
} from "react-router-dom";
import Navbar from "./Components/Navbar/Navbar";
import { Home } from "./Pages/Home/Home";
import { About } from "./Pages/About/About";
import { CarRent } from "./Pages/Car-Rental/CarRent";
import { Contact } from "./Pages/Contact/Contact";
import { Team } from "./Pages/Team/Team";
import { Testimionals } from "./Pages/Testimionals/Testimionals";
import { Faq } from "./Pages/Faq/Faq";
import { Singlepage } from "./Pages/Car-Rental/Cars Container/SinglePage/Singlepage";
import ScrollTop from "./Components/Scroll to top/ScrollTop";
import SignIn from "./Components/Sign-In/SignIn";
import SignUp from "./Components/Sign-Up/SignUp";



function App() {
  return (
    <div className="App">
      <Router>
      <Navbar/>
        <div className="pages">
       <Switch>
            <Route exact path="/" component={Home} />
            <Route path="/rentcar" component={CarRent} />
            <Route path="/team" component={Team} />
            <Route path="/testimionals" component={Testimionals}/>
            <Route path="/about" component={About} />
            <Route path="/contact" component={Contact} />
            <Route path="/faqs" component={Faq} />
            <Route path="/singlepage/:id" component={Singlepage}/>
            <Route path="/signin" component={SignIn} />
            <Route path="/signup" component={SignUp} />
        </Switch>
        <ScrollTop/>
        </div>
      </Router>
 
    </div>
  );
}

export default App;